*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
  color: inherit;
  font-style: normal;
}

a:hover {
  text-decoration: none;
}

@font-face {
  font-family: "Inter";
  src: url("../../fonts/Inter-Regular.woff2") format("woff2"), url("../../fonts/Inter-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../../fonts/Inter-SemiBold.woff2") format("woff2"), url("../../fonts/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
.body {
  font-family: "Inter", sans-serif;
  display: flex;
  flex-direction: column;
  padding: 50px 0px 0px 0px;
}
.body .header {
  justify-items: center;
}
.body .header .header__title {
  font-size: 42px;
  font-weight: 600;
  margin: 0px 0px 10px 0px;
}
.body .header .header__link {
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
}
.body .header .header__link:hover {
  text-decoration: underline;
}
.body .main-content {
  margin: 40px 0px 0px 0px;
  flex: 1 1 auto;
}
.body .main-content .actions-wrapper {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 0px 0px 40px 0px;
}
.body .main-content .actions-wrapper .form {
  display: flex;
  gap: 20px;
}
.body .main-content .contact-list-wrapper {
  display: flex;
  justify-content: center;
  gap: 20px;
}
.body .main-content .contact-list-wrapper .contact-list {
  width: 300px;
}
.body .main-content .contact-list-wrapper .contact {
  background-color: rgba(197, 190, 228, 0.658);
  padding: 5px 10px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgb(157, 113, 182);
  font-size: 16px;
}
.body .main-content .contact-list-wrapper .contact .contact__card-info {
  flex: 1 1 auto;
}
.body .main-content .contact-list-wrapper .contact .contact__button {
  all: unset;
  cursor: pointer;
}
.body .main-content .contact-list-wrapper .contact-list__item {
  font-size: 22px;
  font-weight: 600;
  line-height: 204.54%;
  text-transform: uppercase;
  min-height: auto;
  background-color: rgb(197, 233, 196);
  border-top: 1px solid rgb(71, 150, 69);
  border-bottom: 1px solid rgb(71, 150, 69);
  padding: 0px 35px 0px 15px;
  display: flex;
  justify-content: space-between;
}