// Reset styles
@use '_reset';

// Fonts
@font-face {
  font-family: 'Inter';
  src: url('../../fonts/Inter-Regular.woff2') format('woff2'),
				url('../../fonts/Inter-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Inter';
  src: url('../../fonts/Inter-SemiBold.woff2') format('woff2'),
				url('../../fonts/Inter-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

//styles
.body {
	font-family: 'Inter', sans-serif;
	display: flex;
	flex-direction: column;
	padding: 50px 0px 0px 0px;

	.header{
		justify-items: center;

		.header__title{
			font-size: 42px;
			font-weight: 600;
			margin: 0px 0px 10px 0px;
		}

		.header__link{
			font-size: 20px;
			font-weight: 600;
			text-decoration: none;
			
			&:hover{
				text-decoration: underline;
			}
		}
	}

	.main-content {
		margin: 40px 0px 0px 0px;
		flex: 1 1 auto;

		.actions-wrapper {
			display: flex;
			justify-content: center;
			gap: 20px;
			margin: 0px 0px 40px 0px;

			.form {
				display: flex;
				gap: 20px;
			}
		}

		.contact-list-wrapper {
			display: flex;
			justify-content: center;
			gap: 20px;
			
			.contact-list {
				width: 300px;
			}

			.contact {
					background-color: rgba(197, 190, 228, 0.658);
					padding: 5px 10px;
					display: flex;
					align-items: center;
					border-bottom: 1px solid rgb(157, 113, 182);
					font-size: 16px;

					.contact__card-info {
						flex: 1 1 auto;
					}

					.contact__button {
						all: unset;
						cursor: pointer;
					}
				}

			.contact-list__item {
				font-size: 22px;
				font-weight: 600;
				line-height: 204.54%;
				text-transform: uppercase;
				min-height: auto;
				background-color: rgb(197, 233, 196);
				border-top: 1px solid rgb(71, 150, 69);
				border-bottom: 1px solid rgb(71, 150, 69);
				padding: 0px 35px 0px 15px;
				display: flex;
				justify-content: space-between;
			}
		}
	}
}
